import BootPay from "bootpay-js";
import axios from "axios";

function pay(sellItemList, price, storeName, storeCd) {
    BootPay.request({
        // price: trInfo.Price,
        price: price,
        application_id: process.env.REACT_APP_BOOTPAY_APP_ID,
        // name: trInfo.storeName,
        name: storeName,
        pg: 'nicepay',
        // method: 'card',
        show_agree_window: 0,
        user_info: {
            username: '',
            email: 'rmc2@naver.com',
            addr: ' ',
            phone: '010-8222-9249'
        },
        sellItemList: sellItemList,
        order_id: `${Date.now()}__`,
//        params: {callback1: '   1', callback2: '   2', customvar1234: ' '},
//        account_expire_at: '2018-05-25', //    ( yyyy-mm-dd  .  . )
    }).error(function (data) {
        console.log(data);
    }).cancel(function (data) {
        console.log(data);
    }).ready(function (data) {
        //      .
        console.log(data);
    }).confirm(function (data) {
        //   ,     .
        // -       .
        console.log(data);
        var enable = true; //       
        if (enable) {
            BootPay.transactionConfirm(data); //     .
        } else {
            BootPay.removePaymentWindow(); //         .
        }
    }).close(function (data) {
        //   . (,,   )
        // console.log(data);
    }).done(function (data) {
        window.alert('success');
        //   
        //        .
        let storeCd = 'C0001';
        let saleDt = '20201002';
        let posNo = '03';
        let billNo = '0001';
        let transData = {};

        transData = {"data":data, "sellItemList":sellItemList, 'storeCd':storeCd};
        axios.post('http://localhost:8000/trade/', transData);
    });
}

export default pay;
